import { useState, useEffect } from "react";

import "./Project.scss";
import "./ProjectList.scss";
import YTContainer from "./YTContainer";

export default function Project({ darkActive }) {
  const [selected, setSelected] = useState("featured");
  const [vid, setVID] = useState("");
  const [data, setData] = useState([]);

  useEffect(() => {
    switch (selected) {
      case "featured":
        setData(featuredPortfolio);
        setVID(featuredPortfolio[3].vid);
        break;
      case "ecomm":
        setData(ecommPortfolio);
        setVID(ecommPortfolio[3].vid);
        break;
      case "cowin":
        setData(cowinPortfolio);
        setVID(cowinPortfolio[3].vid);
        break;
      default:
        setData();
    }
  }, [selected]);

  return (
    <div id="project" className={"project " + (darkActive && " dark")}>
      <h1>Projects</h1>
      <ul>
        {list.map((item) => {
          return (
            <li
              key={item.id}
              className={
                "projectList " +
                (selected === item.id
                  ? "projectList active "
                  : "projectList ") +
                (darkActive && " dark")
              }
              onClick={() => setSelected(item.id)}
            >
              {item.title}
            </li>
          );
        })}
      </ul>
      <div className="container">
        {data.map((data) => {
          if (data.id !== 4) {
            return (
              <div key={data.id} className="item">
                <img src={data.img} alt={data.title} />
                <h3>{data.title}</h3>
              </div>
            );
          } else {
            return null;
          }
        })}
      </div>
      <YTContainer videoID={vid} />
    </div>
  );
}

